import React, { useState } from 'react'
import DocLayout from '../components/layout/doc';
import Layout from "../components/layout"
import CodeExample from '../components/CodeExample';
import CodeFormatContext from '../components/contexts/CodeFormatContext';

const fontSizingExample = `\
<div class='text-small'>Lorem ipsum dolor</div>
<div>Lorem ipsum dolor</div>
<div class='text-medium'>Lorem ipsum dolor</div>
<div class='text-big'>Lorem ipsum dolor</div>
`

const fontColorExample = `\
<div class='color-primary'>Lorem ipsum dolor</div>
<div class='color-danger'>Lorem ipsum dolor</div>
<div class='color-info'>Lorem ipsum dolor</div>
`

const TextSection = () => (
    <section>
        <h3>Font sizing</h3>

        <div className='columns'>
            <div className='column'>

                <CodeExample code={fontSizingExample} />
            </div>
            <div className='column padding-left-1 padding-top-1'>
                <div dangerouslySetInnerHTML={{ __html: fontSizingExample }} />
            </div>
        </div>

        <h3>Font color</h3>

        <div className='columns'>
            <div className='column'>

                <CodeExample code={fontColorExample} />
            </div>
            <div className='column padding-left-1 padding-top-1'>
                <div dangerouslySetInnerHTML={{ __html: fontColorExample }} />
            </div>
        </div>


    </section>
)

const modalExample = `\
<div class='modal'>
    <div>
        <header>Mon header</header>
        <div>Mon body</div>
        <footer>Mon footer</footer>
    </div>
</div>
`

const heroExample = `\
<section class="hero background-primary">
    <footer class="container">
        Foot
    </footer>
    <div class="measure">
        <p>
        Mon paragraphe
        </p>
    </div>
    <header>
        Head
    </header>
</section>
`

const DisplaySection = () => {

    const [showModal, setShowModal] = useState(false)

    const toggleShowModal = () => {
        setShowModal(!showModal)
    }

    return (
        <section>

            <h2>Heros</h2>

            <div className={`modal ${showModal && 'active'}`}>
                <div>
                    <header>Mon header</header>
                    <div className='background-white'>Mon body</div>
                    <footer className='text-right'><button onClick={toggleShowModal} className='danger'>Fermer</button></footer>
                </div>
            </div>


            <div className='columns'>
                <div className='column'>

                    <CodeExample code={heroExample} />
                </div>
                <div className='column padding-top-1'>

                    <div dangerouslySetInnerHTML={{ __html: heroExample }} />

                </div>
            </div>

            <h2>Modals</h2>

            <div class={`modal ${showModal && 'active'}`}>
                <div>
                    <header>Mon header</header>
                    <div className='background-white'>Mon body</div>
                    <footer className='text-right'><button onClick={toggleShowModal} className='danger'>Fermer</button></footer>
                </div>
            </div>


            <div className='columns'>
                <div className='column'>

                    <CodeExample code={modalExample} />
                </div>
                <div className='column padding-top-1' style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>

                    <button className='primary' onClick={toggleShowModal}>Click to test</button>
                </div>
            </div>

        </section>
    )
}

const HelpersSection = () => {

    const [size, setSize] = useState('1');
    const [margin, setMargin] = useState('');
    const [sizePadding, setSizePadding] = useState('1');
    const [padding, setPadding] = useState('');

    return (
        <section>


            <div className='columns'>
                <div className='column' style={{ flex: 1 }}>
                    <h3 className='margin-0'>Margin management</h3>

                    <p className='text-small'>The unit used is the line-height.</p>


                    <CodeExample code={`<div class='margin${margin}-${size}'>Hello world</div>`} />


                    <div className='margin-top-0-5'>
                        <button className={`small ${margin === '' && 'primary'}`} onClick={() => { setMargin('') }}>Every margin</button>
                        <button className={`small ${margin === '-top' && 'primary'}`} onClick={() => { setMargin('-top') }}>Top margin</button>
                        <button className={`small ${margin === '-right' && 'primary'}`} onClick={() => { setMargin('-right') }}>Right margin</button>
                        <button className={`small ${margin === '-bottom' && 'primary'}`} onClick={() => { setMargin('-bottom') }}>Bottom margin</button>
                        <button className={`small ${margin === '-left' && 'primary'}`} onClick={() => { setMargin('-left') }}>Left margin</button>
                    </div>

                    <div>

                        <button className={`small ${size === '0-5' && 'primary'}`} onClick={() => { setSize('0-5') }}>0.5</button>
                        <button className={`small ${size === '1' && 'primary'}`} onClick={() => { setSize('1') }}>1</button>
                        <button className={`small ${size === '2' && 'primary'}`} onClick={() => { setSize('2') }}>2</button>
                        <button className={`small ${size === '3' && 'primary'}`} onClick={() => { setSize('3') }}>3</button>
                    </div>

                </div>
                <div className='column' style={{ flex: 1 }}>

                    <div className='background-primary' style={{ display: 'inline-block' }} dangerouslySetInnerHTML={{ __html: `<div class='background-dark margin${margin}-${size}'>Hello world</div>` }} />

                </div>
            </div>


            <div className='columns margin-top-3'>
                <div className='column' style={{ flex: 1 }}>
                    <h3 className='margin-0'>Padding management</h3>

                    <p className='text-small'>The unit used is the line-height.</p>

                    <CodeExample code={`<div class='padding${margin}-${size}'>Hello world</div>`} />

                    <div className='margin-top-0-5'>
                        <button className={`small ${padding === '' && 'primary'}`} onClick={() => { setPadding('') }}>Every margin</button>
                        <button className={`small ${padding === '-top' && 'primary'}`} onClick={() => { setPadding('-top') }}>Top margin</button>
                        <button className={`small ${padding === '-right' && 'primary'}`} onClick={() => { setPadding('-right') }}>Right margin</button>
                        <button className={`small ${padding === '-bottom' && 'primary'}`} onClick={() => { setPadding('-bottom') }}>Bottom margin</button>
                        <button className={`small ${padding === '-left' && 'primary'}`} onClick={() => { setPadding('-left') }}>Left margin</button>
                    </div>

                    <div>
                        <button className={`small ${sizePadding === '0-5' && 'primary'}`} onClick={() => { setSizePadding('0-5') }}>0.5</button>
                        <button className={`small ${sizePadding === '1' && 'primary'}`} onClick={() => { setSizePadding('1') }}>1</button>
                        <button className={`small ${sizePadding === '2' && 'primary'}`} onClick={() => { setSizePadding('2') }}>2</button>
                        <button className={`small ${sizePadding === '3' && 'primary'}`} onClick={() => { setSizePadding('3') }}>3</button>
                    </div>

                </div>
                <div className='column' style={{ flex: 1 }}>

                    <div style={{ display: 'inline-block' }} dangerouslySetInnerHTML={{ __html: `<div class='background-dark padding${padding}-${sizePadding}'>Hello world</div>` }} />

                </div>
            </div>
        </section>
    )
}

const FormsSection = () => {

    const [size, setSize] = useState('medium')
    const [fullwidth, setFullWidth] = useState(' fullwidth')
    const [color, setColor] = useState(' primary')
    const [rounded, setRounder] = useState(' rounded')

    const inputSizeExemple = `\
<input
    value='mbote na yo'
    class='${size}${fullwidth}${color}${rounded}'
    />
`

    return (
        <section>

            <h3 className='margin-0'>Input</h3>

            <div className='columns'>
                <div className='column'>

                    <CodeExample code={inputSizeExemple} />
                </div>
                <div className='column padding-left-1 padding-top-1'>
                    <div dangerouslySetInnerHTML={{ __html: inputSizeExemple }} />
                </div>
            </div>

        </section>
    )
}

const MenuItem = ({ children, onClick }) => (

    <div onClick={onClick} className='box text-center margin-1 hover-primary' style={{ flex: 1 }}>
        {children}
    </div>
)

export default () => {

    const [modalOpen, setModalOpen] = useState(false)
    const [section, setSection] = useState('text')

    const [format, setFormat] = useState(/*localStorage.getItem('code-format') || */'html')

    const changeFormat = format => {
        // localStorage.setItem('code-format', format)
        setFormat(format)
    }

    //style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}
    return (
        <Layout>

            {/* <div className='bottom-1 right-1 height-2 width-2 background-primary text-center cursor-pointer' style={{
                position: 'fixed', borderRadius: '50%', display: 'flex'
                , justifyContent: 'center', alignItems: 'center',
                zIndex: 99
            }}
                onClick={toggleModalOpen}
            >
                {
                    modalOpen ? <i className='fas fa-times' /> : <i className='fas fa-exclamation' />
                }
            </div> */}

            <div className={`modal ${modalOpen && 'active'}`}>
                <div className='background-white' style={{ height: '90%', width: '90%' }}>
                    Cheat Sheet
                </div>
            </div>

            <div className='container margin-top-1 margin-bottom-1'>

                <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center' }}>

                    <MenuItem onClick={() => { setSection('text') }}>
                        <div className='height-2'>
                            <i className='fas fa-paragraph fa-2x' />
                        </div>
                        <small>Text</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('display') }}>
                        <div className='height-2'>
                            <i className='fas fa-th-large fa-2x' />
                        </div>
                        <small>Display</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('forms') }}>
                        <div className='height-2'>
                            <i className='fas fa-cog fa-2x' />
                        </div>
                        <small>Components</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('helpers') }}>
                        <div className='height-2'>
                            <i className='fas fa-tools fa-2x' />
                        </div>
                        <small>Helpers</small>
                    </MenuItem>

                    <MenuItem onClick={() => { setSection('forms') }}>
                        <div className='height-2'>
                            <i className='fas fa-bars fa-2x' />
                        </div>
                        <small>Forms</small>
                    </MenuItem>

                </div>

                <hr />

                <CodeFormatContext.Provider value={{
                    format, setFormat: changeFormat
                }}>

                    {
                        section === 'text' ? <TextSection /> :
                            section === 'display' ?
                                <DisplaySection />
                                : section === 'helpers' ?
                                    <HelpersSection /> :
                                    <FormsSection />
                    }

                </CodeFormatContext.Provider>

            </div>

        </Layout>
    )
}